<template>
  <div>
    <div style="margin-top: 10px;display: flex;justify-content: center">
      <el-select v-model="rvalue" placeholder="请选择员工ID">
        <el-option
          v-for="item in workers"
          :key="item.userid"
          :label="item.userid | handlervalue"
          :value="item.userid"
        ></el-option>
      </el-select>
      <el-button icon="el-icon-search" type="primary" @click="searchbyid(rvalue)">搜索</el-button>
      <el-button icon="el-icon-search" type="danger">显示未分配的任务</el-button>
    </div>
    <div>
      <el-button type="primary" style="margin-left:80%" @click="newtaskVisible = true">新建任务</el-button>
      <el-dialog title="新建任务" :visible.sync="newtaskVisible" width="30%">
        <el-form :rules="rules" ref="tform" :model="tform" label-width="125px">
          <el-form-item label="请输入任务名" prop="ntaskname">
            <el-input v-model="tform.ntaskname"></el-input>
          </el-form-item>
          <el-form-item label="请输入任务描述" prop="ntaskinfo">
            <el-input v-model="tform.ntaskinfo"></el-input>
          </el-form-item>
          <el-form-item label="请输入任务对象" prop="ntaskold">
            <el-input v-model="tform.ntaskold"></el-input>
          </el-form-item>
          <el-form-item label="请输入任务周期" prop="ntaskfrequency">
            <el-select v-model="tform.ntaskfrequency" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="上传任务的图片" prop="uimg">
            <el-upload
              class="avatar-uploader"
              action="http://upload.qiniup.com"
              :show-file-list="false"
              :data="postData"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="dialogVisible = false;newtaskexit();">取 消</el-button>
          <el-button :plain="true" type="primary" @click="dialogVisible = false;newtasks();">确 定</el-button>
        </span>
      </el-dialog>
    </div>
    <div>
      <el-table :data="tableData | handletable" border style="width: 80%">
        <el-table-column prop="tid" label="任务ID" width="100"></el-table-column>
        <el-table-column prop="uid" label="员工ID" width="100"></el-table-column>
        <el-table-column prop="oid" label="任务对象ID" width="100"></el-table-column>
        <el-table-column prop="title" label="任务名" width="200"></el-table-column>
        <el-table-column prop="tfrequency" label="任务频率" width="200"></el-table-column>
        <el-table-column prop="tfinished" label="完成情况" width="200"></el-table-column>
        <el-table-column label="操作" width="400">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="reallocated(scope.row)">重新分配该任务</el-button>
            <el-button size="small" type="danger" @click="deltask(scope.row)">删除任务</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog title="重新分配任务" :visible.sync="reallocatedVisible" width="20%">
        <el-select v-model="rvalue " placeholder="请选择员工ID">
          <el-option
            v-for="item in workers"
            :key="item.userid"
            :label="item.userid | handlervalue"
            :value="item.userid"
          ></el-option>
        </el-select>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="dialogVisible = false;exit();">取 消</el-button>
          <el-button @click="oldinfoVisible = false;setRenponsibility(rvalue);" type="primary">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { genUpToken, token } from "../../utils/qiniuToken";

export default {
  name: "StaPers",
  filters: {
    handlervalue(data) {
      if (data == -1) {
        return "未分配";
      } else {
        return data;
      }
    },
    handletable(data) {
      for (let i = 0; i < data.length; i++) {
        if (data[i].frequency == 1) {
          data[i].tfrequency = "每天一次";
        } else if (data[i].frequency == 7) {
          data[i].tfrequency = "每周一次";
        } else if (data[i].frequency == 30) {
          data[i].tfrequency = "每月一次";
        }

        if (data[i].finished == 0) {
          data[i].tfinished = "未完成";
        } else if (data[i].finished == 1) {
          data[i].tfinished = "已完成";
        }
      }
      return data;
    }
  },
  data() {
    return {
      keywords: "",
      options: [
        { value: 1, label: "每天一次" },
        { value: 7, label: "每周一次" },
        { value: 30, label: "每月一次" }
      ],
      dialogVisible: false,
      tform: {
        ntaskname: "",
        ntaskinfo: "",
        ntaskfrequency: "",
        ntaskold: ""
      },
      rules: {
        ntaskname: [
          { required: true, message: "请输入任务名", trigger: "blur" }
        ],
        ntaskinfo: [
          { required: true, message: "请输入任务信息", trigger: "blur" }
        ],
        ntaskold: [
          { required: true, message: "请输入任务对象", trigger: "blur" }
        ],
        ntaskfrequency: [
          { required: true, message: "请输入任务频率", trigger: "blur" }
        ]
      },
      rvalue: "",
      tableData: [
        { tid: 1, uid: 2, oid: 3, title: "23333", frequency: 7, finished: 1 },
        { tid: 2, uid: 3, oid: 4, title: "2333", frequency: 1, finished: 0 },
        { tid: 3, uid: 5, oid: 6, title: "233", frequency: 30, finished: 1 }
      ],
      workers: [
        {
          userid: 1,
          name: "jjb"
        },
        {
          userid: 21,
          name: "hsq"
        }
      ],
      reallocatedVisible: false,
      newtaskVisible: false,
      imageUrl: "",
      postData: {
        token: ""
      }
    };
  },
  methods: {
    reallocated(data) {
      this.title = "重新分配任务";
      this.reallocatedVisible = true;
    },
    deltask(data) {
      this.$confirm(
        "此操作将永久删除编号为 " + data.tid + " 的任务, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      )
        .then(() => {
          this.$message({
            message: "删除成功",
            type: "success"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    exit() {
      this.reallocatedVisible = false;
    },
    newtaskexit() {
      this.newtaskVisible = false;
    },
    handleAvatarSuccess(res) {
      this.imageUrl = "http://q38gdqayt.bkt.clouddn.com/" + res.key;
      console.log(this.imageUrl);
      // this.EncodedEntryURI = res.key;
    },
    beforeAvatarUpload(file) {
      const isIMG = ["image/jpeg", "image/png", "image/gif"].includes(
        file.type
      );
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isIMG) {
        this.$message.error("上传的头像必须是图片格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isIMG && isLt2M;
    }
  },
  mounted() {
    this.postData.token = token();
    console.log(this.postData.token);
  }
};
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>