<template>
  <div>
    <div>
      <el-button type="primary" style="margin-left:75%" @click="dialogVisible = true">添加新员工</el-button>
      <el-dialog title="添加员工" :visible.sync="dialogVisible" width="30%">
        <el-form :rules="rules" ref="noform" :model="uform" label-width="125px">
          <el-form-item label="请输入员工姓名" prop="noldname">
            <el-input v-model="uform.uname"></el-input>
          </el-form-item>
          <el-form-item label="请输入员工密码" prop="upassword:">
            <el-input v-model="uform.upassword"></el-input>
          </el-form-item>
          <el-form-item label="上传员工的照片" prop="uimg">
            <el-upload
              class="avatar-uploader"
              action="http://upload.qiniup.com"
              :show-file-list="false"
              :data="postData"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="exit()">取 消</el-button>
          <el-button :plain="true" type="primary" @click="newworker()">确 定</el-button>
        </span>
      </el-dialog>
      <el-table :data="tableData" border style="width: 60%">
        <el-table-column prop="uid" label="员工ID" width="300%"></el-table-column>
        <el-table-column prop="name" label="员工姓名" width="300%"></el-table-column>
        <el-table-column label="操作" width="400%">
          <template slot-scope="scope">
            <el-button type="danger" size="small" @click="updpassword(scope.row)">修改密码</el-button>
            <el-button size="small" type="danger" @click="deleteuser(scope.row)">删除员工</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from "../../utils/axios";
import { genUpToken, token } from "../../utils/qiniuToken";

export default {
  name: "StaScore",
  data() {
    return {
      tableData: [{ uid: "22", name: "jjb" }, { uid: "23", name: "hsq" }],
      uform: {
        uname: "",
        upassword: ""
      },
      dialogVisible: false,
      files: [],
      imageUrl: "",
      postData: {
        token: ""
      }
    };
  },
  methods: {
    updpassword(data) {
      this.$prompt("请输入新密码", "修改密码", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: "已经成功修改！"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消输入"
          });
        });
    },
    deleteuser(data) {
      this.$confirm(
        "此操作将永久删除【" + data.name + "】, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      )
        .then(() => {
          this.$message({
            message: "删除成功",
            type: "success"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    handleAvatarSuccess(res) {
      this.imageUrl = "http://q38gdqayt.bkt.clouddn.com/" + res.key;
      console.log(this.imageUrl);
      // this.EncodedEntryURI = res.key;
    },
    beforeAvatarUpload(file) {
      const isIMG = ["image/jpeg", "image/png", "image/gif"].includes(
        file.type
      );
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isIMG) {
        this.$message.error("上传的头像必须是图片格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isIMG && isLt2M;
    },
    newworker() {
      this.dialogVisible = false;
    },
    exit() {
      this.dialogVisible = false;
    }
  },
  mounted() {
    this.postData.token = token();
    console.log(this.postData.token);
  }
};
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>